'use client';

import React from 'react';
import { Loader2, CheckCircle, AlertCircle, Music, Mic, Drum, Guitar, Activity } from 'lucide-react';
import { ProcessingStatusProps } from '../types/audio';
import { formatTime } from '../utils/audio';
import { AUDIO_CONFIG } from '../constants/audio';

const ProcessingStatus: React.FC<ProcessingStatusProps> = ({
  status,
  progress = 0,
  currentStep = '',
  currentStepDetails = '',
  error,
  estimatedTime,
  detectedInstruments = []
}) => {
  const getStatusIcon = () => {
    switch (status) {
      case 'uploading':
      case 'processing':
        return <Loader2 className="h-8 w-8 text-indigo-600 animate-spin" />;
      case 'completed':
        return <CheckCircle className="h-8 w-8 text-green-600" />;
      case 'error':
        return <AlertCircle className="h-8 w-8 text-red-600" />;
      default:
        return null;
    }
  };

  const getStatusText = () => {
    switch (status) {
      case 'uploading':
        return 'Uploading audio file...';
      case 'processing':
        return 'Separating audio tracks...';
      case 'completed':
        return 'Audio separation completed!';
      case 'error':
        return 'Processing failed';
      default:
        return '';
    }
  };

  const getStepIcon = (step: string) => {
    switch (step.toLowerCase()) {
      case 'vocals':
        return <Mic className="h-4 w-4" />;
      case 'drums':
        return <Drum className="h-4 w-4" />;
      case 'bass':
        return <Guitar className="h-4 w-4" />;
      default:
        return <Music className="h-4 w-4" />;
    }
  };

  if (status === 'idle') return null;

  return (
    <div className="w-full max-w-2xl mx-auto space-y-6">
      <div className="bg-white border border-gray-200 rounded-lg p-6 shadow-sm">
        {/* Status Header */}
        <div className="flex items-center space-x-4 mb-6">
          {getStatusIcon()}
          <div>
            <h3 className="text-lg font-medium text-gray-900">
              {getStatusText()}
            </h3>
            {currentStep && (
              <p className="text-sm text-gray-600 flex items-center space-x-2 mt-1">
                <Activity className="h-4 w-4" />
                <span>{currentStep}</span>
              </p>
            )}
            {currentStepDetails && (
              <p className="text-xs text-gray-500 mt-1 animate-pulse">
                {currentStepDetails}
              </p>
            )}
          </div>
        </div>


        {(status === 'uploading' || status === 'processing') && (
          <div className="space-y-3">
            <div className="flex justify-between text-sm text-gray-600">
              <span>Progress</span>
              <span>{Math.round(progress)}%</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div
                className="bg-indigo-600 h-2 rounded-full transition-all duration-300 ease-out"
                style={{ width: `${progress}%` }}
              />
            </div>
            {estimatedTime && (
              <p className="text-xs text-gray-500 text-center">
                Estimated time remaining: {formatTime(estimatedTime)}
              </p>
            )}
          </div>
        )}

        {/* Error Message */}
        {status === 'error' && error && (
          <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg">
            <p className="text-red-700 text-sm">{error}</p>
          </div>
        )}

        {status === 'processing' && detectedInstruments.length > 0 && (
          <div className="mt-6 space-y-3">
            <h4 className="text-sm font-medium text-gray-900">Detected Instruments:</h4>
            <div className="grid grid-cols-1 gap-3">
              {detectedInstruments.map((instrument, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between p-3 rounded-lg border border-green-200 bg-green-50"
                >
                  <div className="flex items-center space-x-2">
                    {getStepIcon(instrument.type)}
                    <div>
                      <span className="text-sm font-medium text-gray-700">{instrument.name}</span>
                      <div className="flex items-center space-x-2 mt-1">
                        <div className="w-16 bg-gray-200 rounded-full h-1.5">
                          <div
                            className="bg-green-500 h-1.5 rounded-full transition-all duration-300"
                            style={{ width: `${instrument.confidence}%` }}
                          />
                        </div>
                        <span className="text-xs text-gray-500">{instrument.confidence}% confidence</span>
                      </div>
                    </div>
                  </div>
                  <CheckCircle className="h-4 w-4 text-green-600" />
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProcessingStatus; 