import { useState, useCallback } from 'react';
import { ProcessingStatus, AudioTrack } from '../types/audio';
import { AUDIO_CONFIG, MOCK_TRACKS } from '../constants/audio';
import axios from 'axios';

interface DetectedInstrument {
  id: string;
  name: string;
  type: 'vocals' | 'piano' | 'guitar' | 'drums' | 'bass' | 'strings' | 'brass' | 'other';
  confidence: number;
  subParts?: string[];
  isSelected: boolean;
}

type ProcessingPhase = 'idle' | 'detecting' | 'selecting' | 'separating' | 'completed' | 'error';

export const useAudioProcessing = () => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [processingPhase, setProcessingPhase] = useState<ProcessingPhase>('idle');
  const [progress, setProgress] = useState(0);
  const [currentStep, setCurrentStep] = useState('');
  const [currentStepDetails, setCurrentStepDetails] = useState('');
  const [error, setError] = useState<string | null>(null);
  const [separatedTracks, setSeparatedTracks] = useState<AudioTrack[]>([]);
  const [detectedInstruments, setDetectedInstruments] = useState<DetectedInstrument[]>([]);
  const [selectedInstruments, setSelectedInstruments] = useState<DetectedInstrument[]>([]);
  const [sessionId, setSessionId] = useState<string | null>(null);
  const [outputSessionId, setOutputSessionId] = useState<string | null>(null);

  // API base URL - change this to your Python service URL
  const API_BASE_URL = process.env.NEXT_PUBLIC_AUDIO_SERVICE_URL || 'http://localhost:5000';

  const handleFileSelect = useCallback((file: File) => {
    setSelectedFile(file);
    setProcessingPhase('idle');
    setError(null);
    setSeparatedTracks([]);
    setDetectedInstruments([]);
    setSelectedInstruments([]);
  }, []);

  const handleFileRemove = useCallback(() => {
    setSelectedFile(null);
    setProcessingPhase('idle');
    setError(null);
    setSeparatedTracks([]);
    setDetectedInstruments([]);
    setSelectedInstruments([]);
    setSessionId(null);
    setOutputSessionId(null);
  }, []);


  const startDetection = useCallback(async () => {
    if (!selectedFile) return;

    setProcessingPhase('detecting');
    setProgress(0);
    setError(null);
    setCurrentStep('');
    setCurrentStepDetails('');

    try {
      const detectionSteps = [
        'Analyzing audio frequency spectrum...',
        'Detecting harmonic patterns...',
        'Identifying rhythmic elements...',
        'Mapping vocal frequency ranges...',
        'Analyzing instrumental signatures...',
        'Cross-referencing with instrument database...',
        'Finalizing detection results...'
      ];

      for (let i = 0; i < detectionSteps.length; i++) {
        setCurrentStep(detectionSteps[i]);
        setProgress((i / detectionSteps.length) * 100);
        await new Promise(resolve => setTimeout(resolve, 1000));
      }

      const detected = [
        {
          id: 'vocals-1',
          name: 'Vocals',
          type: 'vocals' as const,
          confidence: 95,
          subParts: ['Lead Vocals', 'Backing Vocals', 'Harmonies'],
          isSelected: false
        },
        {
          id: 'piano-1',
          name: 'Piano',
          type: 'piano' as const,
          confidence: 88,
          subParts: ['Main Piano', 'Rhythm Piano'],
          isSelected: false
        },
        {
          id: 'guitar-1',
          name: 'Electric Guitar',
          type: 'guitar' as const,
          confidence: 92,
          subParts: ['Lead Guitar', 'Rhythm Guitar'],
          isSelected: false
        },
        {
          id: 'drums-1',
          name: 'Drums',
          type: 'drums' as const,
          confidence: 97,
          subParts: ['Kick Drum', 'Snare', 'Hi-Hats', 'Cymbals'],
          isSelected: false
        }
      ];

      setDetectedInstruments(detected);
      setProcessingPhase('selecting');

    } catch (err) {
      setError('Detection failed. Please try again.');
      setProcessingPhase('error');
    }
  }, [selectedFile]);

  // Handle instrument selection
  const handleInstrumentsSelected = useCallback((instruments: DetectedInstrument[]) => {
    setSelectedInstruments(instruments);
    setProcessingPhase('separating');
    startSeparation(instruments);
  }, []);

  const startSeparation = useCallback(async (instruments: DetectedInstrument[]) => {
    setProcessingPhase('separating');
    setProgress(0);
    setCurrentStep('');
    setCurrentStepDetails('');

    try {
      const totalSteps = instruments.length;
      
      for (let i = 0; i < instruments.length; i++) {
        const instrument = instruments[i];
        setCurrentStep(`Separating ${instrument.name}...`);
        
        // Simulate detailed separation process for each instrument
        const separationSteps = [
          `Analyzing ${instrument.name} frequency range...`,
          `Extracting ${instrument.name} from mix...`,
          `Cleaning ${instrument.name} artifacts...`,
          `Optimizing ${instrument.name} quality...`
        ];

        for (const step of separationSteps) {
          setCurrentStepDetails(step);
          await new Promise(resolve => setTimeout(resolve, 800));
        }


        const stepProgress = ((i + 1) / totalSteps) * 100;
        setProgress(stepProgress);
      }

      // Generate separated tracks based on selected instruments
      const generatedTracks: AudioTrack[] = [];
      
      instruments.forEach(instrument => {
        if (instrument.subParts) {
          instrument.subParts.forEach((part, index) => {
            generatedTracks.push({
              id: `${instrument.id}-${index}`,
              name: part,
              type: instrument.type,
              url: `/api/audio/${instrument.type}-${index}`,
              duration: 180,
            });
          });
        } else {
          generatedTracks.push({
            id: instrument.id,
            name: instrument.name,
            type: instrument.type,
            url: `/api/audio/${instrument.type}`,
            duration: 180,
          });
        }
      });

      setSeparatedTracks(generatedTracks);
      setProcessingPhase('completed');
      setProgress(100);

    } catch (err) {
      setError('Separation failed. Please try again.');
      setProcessingPhase('error');
    }
  }, []);

  const resetAll = useCallback(() => {
    setSelectedFile(null);
    setProcessingPhase('idle');
    setProgress(0);
    setCurrentStep('');
    setCurrentStepDetails('');
    setError(null);
    setSeparatedTracks([]);
    setDetectedInstruments([]);
    setSelectedInstruments([]);
  }, []);

  const handleDownload = useCallback((trackId: string) => {
    console.log(`Downloading track: ${trackId}`);
    // In production, this would trigger a real download
  }, []);

  return {
    selectedFile,
    processingPhase,
    progress,
    currentStep,
    currentStepDetails,
    error,
    separatedTracks,
    detectedInstruments,
    selectedInstruments,
    handleFileSelect,
    handleFileRemove,
    startDetection,
    handleInstrumentsSelected,
    resetAll,
    handleDownload,
  };
}; 